.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
}

.header_logo{
    height: 50px;
object-fit: contain;
margin-left: 20px;
}

.header_input{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid black;
}

.header_icon{
    margin-right: 16px;
}

.header_inputbtn{
    width: 50px !important;
    background-color:#fafafa ;
    border-left: 1px solid lightgray;
    color: gray;
}


.header_icon{
    display: flex;
    align-items: center;
}

.header_left{
    display: flex;
    align-items: center;
}

.header_input input{
    flex: 1;
    border: none;
}